import uniRate from "@/components/uni-ui/uni-rate/uni-rate.vue"
export default {
	components: {
		uniRate
	},
	data() {
		return {
			movie: [],
			flag: true,
			disabled: true,
			performer: [],
			director: [],
		}
	},
	onLoad(options) {
		// 详情页数据
		console.log(JSON.parse(options.item));
		this.movie = JSON.parse(options.item)
		this.movie.plotPics = JSON.parse(this.movie.plotPics)
		// 请求详情导演
		uni.request({
			url: `${this.$url}/search/staff/${this.movie.id}/1?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.director = res.data.data
				console.log(res.data);
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
		// 请求详情演员
		uni.request({
			url: `${this.$url}/search/staff/${this.movie.id}/2?qq=122212489`,
			method: 'POST',
			data: {},
			success: res => {
				this.performer = res.data.data
				console.log(res.data);
			},
			fail: (e) => {
				console.log(e);
			},
			complete: () => {}
		});
	},
	onShareAppMessage() {
		return {
			title: this.movie.name,
			success: (res) => {
				console.log("转发成功", res);
			},
			fail: (res) => {
				console.log("转发失败", res);
			},
			}
		},
		methods: {
			Preservation() {
				this.flag = !this.flag
			},
			long() {
				let that = this
				uni.showActionSheet({
					itemList: ['保存到本地'],
					success: function(res) {
						if (res.tapIndex === 0) {
							uni.downloadFile({
								url: that.movie.poster, //仅为示例，并非真实的资源
								success(res) {
									console.log(res)
									// 只要服务器有响应数据，就会把响应内容写入文件并进入 success 回调，业务需要自行判断是否下载到了想要的内容
									if (res.statusCode === 200) {
										uni.saveImageToPhotosAlbum({
											filePath: res.tempFilePath,
											success: function() {
												uni.showToast({
													title: "保存成功"
												})
											}
										});
									}
								}
							})
						}
					},
					fail: function(res) {
						console.log(res.errMsg);
					}
				});
			}
		},
	}
